"use client"


import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from "@/components/ui/select"
import { useData, Insight } from "@/lib/data-context"
import { useRouter } from "next/navigation"
import { toast } from "sonner"

const formSchema = z.object({
    title: z.string().min(2, {
        message: "Title must be at least 2 characters.",
    }),
    description: z.string().min(10, {
        message: "Description must be at least 10 characters.",
    }),
    author: z.string().min(2, {
        message: "Author name is required.",
    }),
    date: z.string().min(1, {
        message: "Date is required.",
    }),
    readTime: z.string().min(1, {
        message: "Read time is required.",
    }),
    category: z.string().min(1, {
        message: "Category is required.",
    }),
    image: z.string().min(1, {
        message: "Image is required.",
    }),
    iconName: z.string().optional(),
    color: z.string().optional(),
})

interface InsightFormProps {
    initialData?: Insight
    isEditing?: boolean
}

export function InsightForm({ initialData, isEditing = false }: InsightFormProps) {
    const { addInsight, updateInsight } = useData()
    const router = useRouter()

    const form = useForm<z.infer<typeof formSchema>>({
        resolver: zodResolver(formSchema),
        defaultValues: {
            title: initialData?.title || "",
            description: initialData?.description || "",
            author: initialData?.author || "",
            date: initialData?.date || "",
            readTime: initialData?.readTime || "",
            category: initialData?.category || "",
            // Default values for hidden fields
            iconName: initialData?.iconName || "Lightbulb",
            color: initialData?.color || "#7B3FEF",
            image: initialData?.image || "",
        },
    })

    const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0]
        if (file) {
            const imageUrl = URL.createObjectURL(file)
            form.setValue("image", imageUrl)
        }
    }

    function onSubmit(values: z.infer<typeof formSchema>) {
        const insightData = {
            ...values,
            href: initialData?.href || `/insights/${values.title.toLowerCase().replace(/\s+/g, "-")}`,
        }

        if (isEditing && initialData) {
            updateInsight(initialData.id, insightData)
            toast.success("Insight updated successfully")
        } else {
            addInsight(insightData)
            toast.success("Insight created successfully")
        }

        router.push("/dashboard/insights")
        router.refresh()
    }

    return (
        <Form {...form}>
            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8 max-w-2xl">
                <FormField
                    control={form.control}
                    name="title"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Title</FormLabel>
                            <FormControl>
                                <Input placeholder="Insight Title" {...field} />
                            </FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                <FormField
                    control={form.control}
                    name="description"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Description</FormLabel>
                            <FormControl>
                                <Textarea placeholder="Insight Description" {...field} />
                            </FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />

                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    <FormField
                        control={form.control}
                        name="author"
                        render={({ field }) => (
                            <FormItem>
                                <FormLabel>Author</FormLabel>
                                <FormControl>
                                    <Input placeholder="Author Name" {...field} />
                                </FormControl>
                                <FormMessage />
                            </FormItem>
                        )}
                    />
                    <FormField
                        control={form.control}
                        name="category"
                        render={({ field }) => (
                            <FormItem>
                                <FormLabel>Category</FormLabel>
                                <FormControl>
                                    <Input placeholder="Technology" {...field} />
                                </FormControl>
                                <FormMessage />
                            </FormItem>
                        )}
                    />
                </div>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    <FormField
                        control={form.control}
                        name="date"
                        render={({ field }) => (
                            <FormItem>
                                <FormLabel>Date</FormLabel>
                                <FormControl>
                                    <Input placeholder="Jan 01, 2025" {...field} />
                                </FormControl>
                                <FormMessage />
                            </FormItem>
                        )}
                    />
                    <FormField
                        control={form.control}
                        name="readTime"
                        render={({ field }) => (
                            <FormItem>
                                <FormLabel>Read Time</FormLabel>
                                <FormControl>
                                    <Input placeholder="5 min read" {...field} />
                                </FormControl>
                                <FormMessage />
                            </FormItem>
                        )}
                    />
                </div>

                {/* Hidden fields for icon and color */}
                <input type="hidden" {...form.register("iconName")} />
                <input type="hidden" {...form.register("color")} />

                <FormField
                    control={form.control}
                    name="image"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Insight Image</FormLabel>
                            <FormControl>
                                <div className="space-y-4">
                                    <Input
                                        type="file"
                                        accept="image/*"
                                        onChange={handleImageUpload}
                                    />
                                    {field.value && (
                                        <div className="relative w-full h-48 rounded-lg overflow-hidden border">
                                            <img
                                                src={field.value}
                                                alt="Preview"
                                                className="object-cover w-full h-full"
                                            />
                                        </div>
                                    )}
                                    {/* Hidden input to store the URL */}
                                    <input type="hidden" {...field} />
                                </div>
                            </FormControl>
                            <FormDescription>
                                Upload a cover image for the insight.
                            </FormDescription>
                            <FormMessage />
                        </FormItem>
                    )}
                />

                <Button type="submit">{isEditing ? "Update Insight" : "Create Insight"}</Button>
            </form>
        </Form>
    )
}
